
.footersWarp{width: 85%;height: .93rem;background: #FFFFFF;padding-left: 15%;
}
.footersWarp .top{height: .46rem;border-bottom: 1px solid #EFEFF4;position: relative;
}
.footersWarp .top .left{float: left;
}
.footersWarp .top .left img{width: .3rem;float: left;position: absolute;top: .07rem;left: -.35rem;
}
.footersWarp .top .left p{float: left;height: .46rem;line-height: .46rem;
}
.footersWarp .top .right{float: right;
}
.footersWarp .top .right span{height: .46rem;line-height: .46rem;
}
.footersWarp .top .right img{width: .1rem;height: .14rem;margin-right: .1rem;
}
.footersWarp .bot{height: .46rem;position: relative;
}
.footersWarp .bot .left{float: left;
}
.footersWarp .bot .left img{width: .3rem;float: left;position: absolute;top: .07rem;left: -.35rem;
}
.footersWarp .bot .left p{float: left;height: .46rem;line-height: .46rem;
}
.footersWarp .bot .right{float: right;
}
.footersWarp .bot .right span{height: .46rem;line-height: .46rem;
}
.footersWarp .bot .right img{width: .1rem;height: .14rem;margin-right: .1rem;
}



