
.consWarp{width: 90%;height: 1.15rem;background: #FFFFFF;padding: 0 5%;margin: 10px 0;color: #787878;
}
.consWarp .top{height: .5rem;border-bottom: 1px solid #EFEFF4;
}
.consWarp .top .left{float: left;
}
.consWarp .top .left img{width: .29rem;float: left;margin-top: .1rem;
}
.consWarp .top .left p{float: left;height: .5rem;line-height: .5rem;
}
.consWarp .top .right{float: right;
}
.consWarp .top .right span{height: .5rem;line-height: .5rem;
}
.consWarp .top .right img{width: .1rem;height: .14rem;
}
.consWarp .bot ul{float: left;width: 100%;text-align: center;font-size: .14rem;
}
.consWarp .bot ul li{float: left;width: 27%;padding: 10px;
}



