
.categoryWrap{font-size: 16px;background: #EFEFF4;position: relative;
}
.categoryWrap ul{width: 26%;height: 5.68rem;background: #FFFFFF;float: left;overflow: auto;position: absolute;left: 0;top: .44rem;
}
.categoryWrap ul li{width: 90%;padding: .1rem 0 .1rem .1rem;border-bottom: 1px solid #eeeeee;
}
.categoryWrap ul li a{color: #000000;
}
.categoryWrap .aivde{background: #EFEFF4;
}
.categoryWrap .aivde a{color: #ED2F38;
}
.categoryWrap .right{float: right;width: 74%;height: 5.68rem;overflow: auto;
}



